import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DeletedFamilyComponent } from './deletedFamily.component';

const routes: Routes = [
    {
        path: '',
        component: DeletedFamilyComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class DeletedFamilyRoutingModule {}
